.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */ /* Safari/Chrome, other WebKit */ /* Firefox, other Gecko */
  box-sizing: border-box;
}

.modal-container {
  background-color: white;
  width: 90%;
  margin: 2% auto;
  height: 75%;
  /* padding: 20px; */
  border: 10px solid turquoise;
  z-index: 2;
  position: fixed;
  left: 5%; /* Safari/Chrome, other WebKit */ /* Firefox, other Gecko */
  box-sizing: border-box;
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  margin: 2% auto; /* 15% from the top and centered */
  border-top: 10px solid turquoise;
  border-bottom: 10px solid turquoise;
  width: 70%; /* Could be more or less, depending on screen size */
  height: 75%;
  font-size: 25px;
  z-index: 3;
  position: fixed;
  left: 15%;
  padding-left: 40px;
  padding-right: 40px;
  line-height: 1em; /* Safari/Chrome, other WebKit */ /* Firefox, other Gecko */
  box-sizing: border-box;
  font-family: "Open Sans Condensed", sans-serif;
}

/* The Close Button */
.close {
  color: #aaa;
  /* float: right; */
  position: fixed;
  left: 88.8%;
  font-size: 40px;
  font-weight: bold;
  z-index: 4;
  transition: all 0.2s ease;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
  font-size: 43px;
  transition: all 0.2s ease;
}

